@import 'reset';


* {
	box-sizing: border-box;
}

body{
	background-color: #fff;
}


.nav {
	display: none;
	height: 0;
	z-index: 4;
}

.nav ul li {
	text-align: center;
	margin-bottom: 12%;
	margin-top: 4%;
}

@media screen and (min-width: 768px) {
	.nav ul li {
		margin-top: 67px;
		margin-bottom: 0%;
	}
}

.nav ul li a {
	text-decoration: none;
	text-transform: uppercase;
	letter-spacing: 0.08em;
	color: #565583;
	font-family: 'Cabin Sketch', cursive;
	transition: .4s ease-in-out;
	font-size: 2em;
}

@media screen and (min-width: 768px) {
	.nav ul li a {
		font-size: 2.9em;
	}
}

@media screen and (min-width: 1024px) {
	.nav ul li a {
		font-size: 3.3em;
	}
}

.nav ul li a:hover {
	color: #fff;
}

.nav.open {
	display: flex;
	height: 100vh;
	justify-content: center;
	flex-direction: column;
	margin: 0px auto;
	background-color: #d8ddef;
}

.hero-section {
	width: 100%;
	height: 100vh;
	overflow: hidden;
	margin-bottom: 20%;
}

@media screen and (min-width: 1440px) {
