.hero-section .topLine .burger {
	z-index: 6;
	position: absolute;
	right: 176px;
	top: 60px;
	cursor: pointer;
	transition: .2s ease-in-out;
}

.hero-section .topLine .burger:hover {
	transform: translateY(-5px);
}

.hero-section .topLine .burger .top-line {
	position: absolute;
	background-color: #825aa6;
	height: 7px;
	width: 67px;
	left: 64px;
	border-radius: 20px;
	transition: .4s ease-in-out;
}

.hero-section .topLine .burger .bottom-line {
	top: 22px;
	position: absolute;
	background-color: #93a1d2;
	height: 7px;
	width: 45px;
	left: 86px;
	border-radius: 20px;
	transition: .4s ease-in-out;
}

.hero-section .topLine .burger.open div:first-child {
	width: 45px;
	transform: translateX(20px) rotate(-45deg);
	transition: .4s ease-in-out;
}

.hero-section .topLine .burger.open div:last-child {
	transition: .4s ease-in-out;
	transform: translateY(-23px) rotate(45deg);
}

.hero-section .topLine .burger.open .nav {
	display: flex;
}

.hero-section .back {
	position: absolute;
	left: 40px;
	top: 60px;
}

.hero-section .back a em.fas.fa-chevron-left {
	transition: .2s ease-in-out;
	color: #541b8e;
	font-size: 2em;
}

.hero-section .back a em.fas.fa-chevron-left:hover {
	color: #93a1d2;
}

