.portfolio-popUp-overlay {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(42, 41, 43, 0.812);
	opacity: 0.5;
}

.portfolio-popUp-overlay.show {
	display: block;
	transition: .4s ease-in-out;
}

.portfolio-popUp {
	display: none;
	background-color: #fff;
	border: 2px solid gray;
	z-index: 12;
	overflow-y: auto;
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	margin: 0;
	max-height: 90vh;
	width: 60%;
}

@media screen and (max-width: 767px) {
	.portfolio-popUp {
		width: 90%;
	}
}

.portfolio-popUp div .x {
	top: 30px;
	font-size: 1.2em;
	right: 30px;
	position: absolute;
	color: gray;
	transition: .2s ease-in-out;
}

.portfolio-popUp div .x:hover {
	top: 28px;
	right: 28px;
	color: #fff;
	background-color: gray;
	padding: 1px 5px;
	border-radius: 90%;
}

.portfolio-popUp.show {
	display: block;
	transition: .4s ease-in-out;
}

